'use strict';
const EnforcerRef  = require('../enforcer-ref');

module.exports = {
    init: function (data) {

    },

    prototype: {},

    validator: function (data) {
        return {
            type: 'object',
            properties: {
                authorizationUrl: {
                    allowed: ({ parent }) => parent && ['implicit', 'authorizationCode'].includes(parent.key),
                    required: true,
                    type: 'string'
                },
                refreshUrl: {
                    type: 'string'
                },
                scopes: {
                    required: true,
                    type: 'object',
                    additionalProperties: {
                        type: 'string'
                    }
                },
                tokenUrl: {
                    allowed: ({ parent }) => parent && ['password', 'clientCredentials', 'authorizationCode'].includes(parent.key),
                    required: true,
                    type: 'string'
                }
            }
        }
    }
};
